.menuWrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: #f8f8f8;
	height: 100%;
}

.orgContainer {
	height: 75px;
	width: 100%;
	background-color: #ffffff;
	margin-top: 20px;
	padding: 0 30px;

	display: flex;
	justify-content: space-between;
	align-items: center;
}

.trainerDiv {
	display: flex;
	align-items: center;
}

.trainerPhoto {
	height: 45px;
	width: 45px;
	border-radius: 50%;
	margin-right: 12px;
	overflow: hidden;
}
.trainerPhoto img {
	width: 100%;
}

.trainerName {
	font-family: "Inter", sans-serif;
	font-size: 14px;
	font-weight: 600;
	line-height: 17px;
	letter-spacing: 0em;
}
.trainerName * {
	display: block;
	font-weight: 600;
}

.orgLogo {
	width: 89px;
	height: 45px;
	border: 1px solid rgba(102, 112, 133, 0.5);
	border-radius: 3px;

	display: flex;
	align-items: center;
}
.orgLogo img {
	width: 100%;
}

@keyframes fadeOut {
	0% {
		opacity: 1;
	}
	80% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}
