.container {
	display: flex;
	flex-direction: column;
}

.numberScanned {
	display: flex;
	justify-content: space-between;
	padding: 0 25px;
	align-items: center;
	height: 40px;
	font-family: "Inter", sans-serif;
	font-size: 12px;
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
	color: #667085;
}
.numberScanned span {
	display: flex;
	align-items: center;
	font-weight: 600;
}
.numberScanned svg {
	margin: 0 5px;
}

.listContainer {
	margin: 36px 22px 100px 22px;
}

.scanList {
	list-style: none;
}

.noScanned {
	text-align: center;
	font-family: "Inter", sans-serif;
	font-size: 13px;
	opacity: 0.7;
}
.noScannedText {
	font-weight: 500;
}

.nextBtnDiv {
	display: flex;
	justify-content: center;
	position: fixed;
	bottom: 0;
	background-color: #ffffff;
	width: 100%;
	padding: 7px 23px 26px 23px;
}
.nextBtnDiv * {
	display: flex;
	align-items: center;
	justify-content: center;

	height: 45px;
	width: 100%;
	border-radius: 3px;

	color: #fff;
	font-family: "Inter", sans-serif;
	font-size: 14px;
	font-weight: 600;
	text-decoration: none;
}
.nextBtnEnabled {
	background-color: #ff6637;
}
.nextBtnEnabled:hover {
	background-color: #ff5b2a;
}
.nextBtnDisabled {
	background-color: #b2b2b2;
	pointer-events: none;
}
