.container {
	display: flex;
	flex-direction: column;
	/* justify-content: center; */
	/* align-items: center; */
	height: 70%;
	padding: 20px;
}

.card {
	width: 100%;
	max-width: 400px;
	background-color: inherit;
	border-radius: 8px;
	margin-top: 60px;
	/* box-shadow: 0 0px 4px rgba(0, 0, 0, 0.1); */
}

.header {
	margin-bottom: 2rem;
}
.heading {
	display: block;
	color: #140e34;
	font-size: 28px;
	/* margin-top: 0.67em; */
	margin-bottom: 1em;
	font-family: "Inter", sans-serif;
	font-weight: 600;
}

.welcomeText {
	display: block;
	margin-top: -10px;
	font-size: 16px;
	font-family: "Inter", sans-serif;
}
.welcome {
	font-weight: 500;
}
.welcome span {
	font-weight: 700;
}
.emoji {
	font-weight: 100;
}

.formGroup {
	margin-bottom: 1rem;
	height: 90px;
}

label {
	display: block;
	margin-bottom: 0.5rem;
	font-family: "Inter", sans-serif;
	font-weight: 500;
	color: #140e34;
	opacity: 50%;
}

.inputGroup {
	display: flex;
	align-items: center;
	background-color: #f5f5f5;
	border-radius: 4px;
	box-sizing: border-box;
	border: 1px solid rgb(102, 112, 133, 0.5);
}
.inputField:focus {
	outline: none;
}
.inputGroup:focus-within {
	border: 1px solid #ff6637;
}

.inputIcon {
	display: flex;
	align-items: center;
	height: 43px;
	padding: 0.5rem;
	background-color: #ffffff;
	color: #667085;
	border-radius: 3px 0 0 3px;
	border-right: none;
}

.inputField {
	width: 100%;
	height: 43px;
	padding: 6px 12px 6px 12px;
	font-size: 16px;
	font-family: "Inter", sans-serif;
	font-weight: 500;
	border-radius: 0 3px 3px 0;
	flex: 1;
	padding: 0.5rem;
	border-left: none;
}
.inputPwd {
	border-radius: 0;
	border-right: none;
}
.pwdToggle {
	display: flex;
	align-items: center;
	height: 43px;
	padding: 0.5rem;
	background-color: #ffffff;
	color: #667085;
	border-radius: 0 3px 3px 0;
	border-left: none;
}

input::placeholder {
	font-size: 12px;
	font-family: "Inter", sans-serif;
	font-weight: 500;
	color: #667085;
}

.error {
	color: #dc3545;
	margin: 0.5rem 0;
	font-size: 12px;
	font-family: "Inter", sans-serif;
}

.loginBtn {
	text-align: center;
}
.submit {
	display: flex;
	justify-content: center;
	align-items: center;

	background-color: #ff6637;
	width: 100%;
	height: 45px;
	color: #ffffff;
	font-family: "Inter", sans-serif;
	font-size: 14px;
	font-weight: 600;
	border-radius: 3px;
}

.submit:hover {
	background-color: #e0582e;
}
