.employeeDiv {
	display: flex;
	align-items: center;
	border: 1px solid #140e341c;
	box-shadow: 0px 4px 6px 0px #11174914;
	border-radius: 7px;
	margin-bottom: 20px;
	font-family: "Inter", sans-serif;
	font-size: 10px;
	font-weight: 600;
	line-height: 12px;
	letter-spacing: 0em;
	text-align: left;
}

.employeePhoto {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 80px;
	height: 80px;
	padding: 10px;
}

.employeePhoto img {
	width: 60px;
	height: 60px;
	object-fit: cover;
}

.employeeInfo {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	flex-grow: 1;
}

.infoItem {
	margin-bottom: 4px;
}
.infoItem span {
	font-family: "Inter", sans-serif;
	font-size: 10px;
	font-weight: 600;
	line-height: 17px;
}

.infoName {
	letter-spacing: 0em;
	color: rgba(20, 14, 52, 0.8);
}
.infoEmployer {
	letter-spacing: 0.01em;
	color: rgba(20, 14, 52, 0.9);
}
.infoTrade {
	letter-spacing: 0.03em;
	color: rgba(17, 23, 73, 0.5);
}

.deleteBtn {
	display: flex;
	justify-content: center;
	align-items: center;
	color: #333;
	width: 20px;
	height: 20px;
	margin-left: auto;
	margin-right: 7px;
	cursor: pointer;
}
