.searchDiv {
	display: flex;
	align-items: center;
	width: 100%;
	height: 36px;
	border-radius: 8px;
}
.searchBar {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex: 1;
	height: 100%;
	background-color: #f8f8f8;
	border-radius: 6px;
	/* margin-right: 6px; */
}
.searchInput:focus {
	outline: none;
}
.searchBar:focus-within {
	background-color: #f3f3f3;
}

.clearSearch {
	display: flex;
	justify-content: center;
	align-items: center;

	width: 24px;
	height: 24px;
	margin: 0 5px;

	color: rgba(20, 14, 52, 0.4);
	cursor: pointer;
}

.searchIcon {
	display: flex;
	align-items: center;
	justify-content: center;
	color: rgba(20, 14, 52, 0.2);
	padding: 10px;
}

.searchInput {
	height: 100%;
	width: 100%;
	background: #f8f8f8;
	color: rgba(20, 14, 52, 0.6);
	font-family: "Poppins", sans-serif;
	font-size: 13px;
	font-weight: 600;
	margin-right: 2px;
}
.searchInput::placeholder {
	font-family: "Poppins", sans-serif;
	font-size: 13px;
	font-weight: 600;
	color: rgba(20, 14, 52, 0.4);
}

.searchInput:focus {
	background-color: inherit;
}
