.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 20px;
	margin-bottom: 100px;
}

.searchContainer {
	width: 100%;
	margin-top: 14px;
	margin-bottom: 24px;
}

.courseList {
	display: flex;
	justify-content: center;
	width: 100%;
}

ul {
	list-style: none;
	width: 100%;
}

.noCoursesText {
	display: block;
	text-align: center;
	padding: 5px 12px;

	font-family: "Inter", sans-serif;
	font-size: 14px;
	color: #667085;
}

.submitBtnContainer {
	position: fixed;
	bottom: 0;
	background-color: #ffffff;
	width: 100%;
	padding: 7px 23px 26px 23px;

	display: flex;
	align-items: center;
	justify-content: center;
}
.submitDiv {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
}
.submitBtn {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 45px;
	width: 100%;

	background-color: #ff6637;
	color: #ffffff;
	font-family: "Inter", sans-serif;
	font-weight: 600;
	font-size: 14px;

	border-radius: 3px;
}
.submitBtn:disabled,
.submitBtn:disabled:hover,
.submitBtn:disabled:active {
	background-color: #b2b2b2;
	cursor: not-allowed;
}
.submitBtn:hover {
	background-color: #ff5b2a;
}
.submitBtn:active {
	background-color: #e0582e;
}

.modalContainer {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	height: 100%;
}

.modalImg {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 90px;
	height: 90px;
}
.modalImg img {
	width: 100%;
}

.modalContent {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin-bottom: 16px;
	font-family: "Inter", sans-serif;
}

.modalTitle {
	font-weight: 600;
	font-size: 16px;
	margin-bottom: 8px;
}
.modalText {
	font-size: 12px;
	line-height: 22px;
	color: #667085;
}
.modalText p {
	font-weight: 500;
}
.modalText a {
	color: inherit;
	font-weight: 600;
}

.modalBtn {
	width: 100%;
	height: 37px;
	border-radius: 3px;
	color: #f5f5f5;
	font-family: "Inter", sans-serif;
	font-size: 14px;
	font-weight: 600;
}
.modalSuccess {
	background-color: #20be79;
}
.modalFailed {
	background-color: #fe595c;
}
