.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 30px 20px;
}

.trainingEventsList,
.trainingEventsListItem {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
}

.noContentContainer {
	height: calc(100% - 70px);
	justify-content: center;
}

.noContent {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	width: 80%;
}

.noContentImg {
	width: 155px;
	height: 159px;
	margin-bottom: 28px;
}
.noContentImg img {
	width: 100%;
}

.noContentInfo {
	display: flex;
	flex-direction: column;
	font-family: "Inter", sans-serif;
}

.noContentTitle {
	font-family: "Inter", sans-serif;
	font-size: 20px;
	font-weight: 700;
	line-height: 28px;
	letter-spacing: 0em;
	margin-bottom: 12px;
}
.noContentText {
	font-family: "Inter", sans-serif;
	font-size: 16px;
	font-weight: 700;
	line-height: 23px;
	letter-spacing: 0em;
	color: rgba(133, 133, 133, 1);
}
