.msgContainer {
	/* background-color: #333; */
	display: flex;
	align-items: center;
	justify-content: center;
	height: 70px;
	width: 100%;
	margin-top: 20px;
}
.loginMsg {
	word-wrap: break-word;
	border-radius: 4px;
	padding: 5px 13px;
	opacity: 0.8;

	font-family: "Poppins", sans-serif;
	font-size: 12px;

	transition: opacity 0.5s;
}
.success {
	background-color: #c8e6c9;
	color: #1b5e20;
}
.failed {
	background-color: #ffcdd2;
	color: #b71c1c;
}

.fadeOut {
	opacity: 0;
	transition: opacity 0.5s;
	animation: fadeOut 2s ease-in-out;
}

@keyframes fadeOut {
	0% {
		opacity: 1;
	}
	80% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}
