.trainingEventDiv {
	display: flex;
	flex-direction: column;
	width: 100%;
	/* max-width: 346px; */
	height: 105px;
	margin-bottom: 25px;
	border: 1px solid rgba(20, 14, 52, 0.11);
	border-radius: 3px;

	font-family: "Inter", sans-serif;
}

.eventHeader {
	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 0 8px;
	height: 25px;
	background-color: #f9f9fa;

	font-size: 9px;
	line-height: 11px;
	letter-spacing: 0.04em;
}

.courseId {
	font-weight: 600;
	color: #ff6637;
}
.eventDateAndTime {
	display: flex;
	align-items: center;
	font-weight: 600;
	color: #667085;
}
.clockSvg {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0 3px;
}

.eventBody {
	height: 80px;
}

.courseName {
	display: flex;
	align-items: center;
	margin: 5px 0;
	padding: 0 9px;
	height: 30px;

	font-size: 12px;
	font-weight: 600;
	line-height: 16px;
	letter-spacing: 0em;
}

.bottomDiv {
	display: flex;
	justify-content: space-between;
}

.trainerAndTrainees {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100%;
	padding: 0 9px;
	margin-bottom: 5px;

	font-size: 9px;
	font-weight: 600;
	line-height: 11px;
	letter-spacing: 0.04em;
}

.svgContainer {
	display: inline-block;
}

.trainerName,
.numOfTrainees {
	display: flex;
	align-items: center;
	height: 17px;

	font-size: 9px;
	font-weight: 600;
	line-height: 11px;
	letter-spacing: 0.04em;
	color: #667085;
}

.trainerAndTraineesSvg {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 11px;
	margin-right: 3px;
}

.eventStatus {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 17px;
	width: fit-content;
	padding: 0 5.5px;
	border-radius: 1em 0 0 1em;

	font-size: 9px;
	font-weight: 600;
	line-height: 11px;
	letter-spacing: 0.04em;

	margin-top: auto;
	margin-bottom: 5px;
}
.complete {
	background-color: rgba(0, 169, 88, 0.08);
	color: #00a958;
}
.pending {
	background-color: rgba(255, 245, 231, 1);
	color: #ffb346;
}
