.courseDiv {
	display: flex;
	flex-direction: column;
	margin-bottom: 20px;
	border: 1px solid #140e341c;
	border-radius: 7px;
	box-shadow: 0px 4px 6px 0px #11174914;
	padding: 10px;
	font-family: "Inter", sans-serif;
	cursor: default;
	width: 100%;
}
.selected {
	background-color: rgba(255, 250, 249, 0.7);
	border: 1px solid rgba(255, 102, 55, 1);
	box-shadow: none;
}

.courseChip {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0 6px;
	width: fit-content;
	height: 17px;
	background-color: #ff663714;
	color: #ff6637;

	font-size: 9px;
	font-weight: 600;
	line-height: 11px;
	letter-spacing: 0.04em;

	margin-bottom: 8px;
}

.courseSerialNo,
.courseTitle {
	font-size: 12px;
	font-weight: 600;
	margin-bottom: 5px;
	color: rgba(20, 14, 52, 0.75);
}
.courseDescription {
	font-size: 10px;
	font-weight: 600;
	line-height: 15px;
	letter-spacing: 0em;

	color: rgba(20, 14, 52, 0.59);
}
