.container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
}

.content {
	display: block;
	text-align: center;
	background-color: #d6d6d6;
	width: 80%;
	border-radius: 7px;
	font-family: "Inter", sans-serif;
}

.title {
	font-size: 6rem;
	margin-bottom: 1rem;
}

.text {
	font-size: 16px;
	margin-bottom: 2rem;
	padding: 0 30px;
	word-wrap: normal;
}

.link {
	margin: 40px;
	text-decoration: none;
	color: #ff6637;
	font-weight: 600;
	font-family: "poppins", sans-serif;
}
