.floatingMenuToggleBtn {
	width: 45px;
	height: 45px;
	border-radius: 50%;
	background-color: #140e34;
	box-shadow: 0px 4px 8px 0px #00000040;

	display: flex;
	justify-content: center;
	align-items: center;

	position: fixed;
	right: 7%;
	bottom: 11%;

	color: #ffffff;
	font-family: "Inter", sans-serif;
	font-size: 23px;
	font-weight: 500;
	line-height: 17px;
	letter-spacing: 0em;
	text-align: left;

	cursor: pointer;
}
.floatingMenuToggleBtn:hover,
.floatingMenuToggleBtn:focus {
	background-color: #140e34e8;
	box-shadow: 0px 3px 7px 0px #00000040;
}

.floatingMenuHidden {
	display: none;
}

.floatingMenuCard {
	width: 232px;
	height: 135px;
	border-radius: 3px;
	background-color: #ffffff;
	box-shadow: 0px 5px 10px 0px #140e340d;
	padding: 9px 0;

	display: flex;
	flex-direction: column;

	position: fixed;
	right: 7%;
	bottom: calc(11% + 45px + 15px);

	font-family: "Inter", sans-serif;
}

.floatingMenuCardTitle {
	font-size: 10px;
	font-weight: 600;
	line-height: 17px;
	letter-spacing: 0em;
	margin-bottom: 8px;
	padding: 0 17px;
}

.floatingMenuCardList {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.floatingMenuCardItem {
	display: flex;
	align-items: center;
	height: 42px;
	width: 100%;
	padding: 0 17px;
}

.floatingMenuItemLink {
	text-decoration: none;
	color: inherit;
}

.floatingMenuCardItem:hover {
	background-color: #ff66370a;
}
.floatingMenuCardItem:hover .floatingMenuCardItemIcon {
	background-color: #ff6637;
}
.floatingMenuCardItemIcon {
	min-width: 25px;
	height: 25px;
	border-radius: 50%;
	background-color: #ff66371a;
	margin-right: 8px;

	display: flex;
	align-items: center;
	justify-content: center;
}
.floatingMenuIcon1,
.floatingMenuIcon2 {
	fill: #ff6637;
}
.floatingMenuCardItem:hover .floatingMenuIcon1,
.floatingMenuCardItem:hover .floatingMenuIcon2 {
	fill: #ffffff;
}

.floatingMenuItemText {
	font-size: 9px;
	font-weight: 600;
	line-height: 11px;
	letter-spacing: 0em;
}
