.container {
	margin: 20px;
}

.infoBox {
	display: block;
	justify-content: center;
	margin-top: 40px;
	background-color: #f8f8f8;
	padding: 10px;
	border-radius: 7px;
}
.infoTitle {
	display: block;
	padding: 10px 10px 5px;
	text-align: center;
	font-family: "Poppins", sans-serif;
	font-size: 17px;
	font-weight: 600;
}
.infoDetails {
	display: block;
	padding: 5px 40px 10px;
	text-align: center;
	word-wrap: normal;
	font-family: "Poppins", sans-serif;
	font-size: 12px;
	font-weight: 500;
}

.scanWindow {
	margin-top: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.videoContainer {
	width: 95%;
	max-width: 400px;
	position: relative;
	overflow: hidden;
	border-radius: 10px;
	aspect-ratio: 1/1; /* Set the aspect ratio to 1:1 */
}

.videoContainer video {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}
