.container {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	max-width: 428px;
	/* max-height: 926px; */
	width: 100%;
	height: 100%;
	overflow: hidden;
	background-color: #ffffff;
}

.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 70px;
	padding: 0 23px;
	background-color: #140e34;
	z-index: 1;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header .backBtnContainer,
.header .otherBtnContainer {
	width: 20px;
}
.header .backBtnContainer button {
	background-color: rgba(0, 0, 0, 0);
	cursor: pointer;
}

.backBtn,
.otherBtn {
	display: flex;
	justify-content: center;
	align-items: center;

	cursor: pointer;
}

.login {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 70px;
	background-color: #ffffff;
	z-index: 1;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 20px;
}

.heading {
	display: flex;
	justify-content: center;
	color: white;
	font-weight: 600;
	font-size: 18px;
	font-family: "Inter", sans-serif;
}
.logo {
	width: 107px;
	text-align: center;
}
.logo img {
	width: 100%;
}

.content {
	margin-top: 70px;
	height: calc(100vh - 70px);
	overflow: auto;
	background-color: #ffffff;

	display: flex;
	flex-direction: column;
}

.children {
	flex: 1;
}

.footer {
	padding: 15px;
	width: 100%;
	font-family: "Inter", sans-serif;
	text-align: center;
	font-size: 9px;
	color: #73788b;
}
.links {
	margin-top: 0.35em;
}
.footer a {
	text-decoration: none;
	cursor: pointer;
	color: rgba(17, 85, 204, 1);
	box-sizing: border-box;
	padding: 0 8px;
}
.leftLink {
	border-right: 1px solid #73788b;
}

.modalContainer {
	display: flex;
	flex-direction: column;
	/* justify-content: space-between; */
	height: 100%;
}
.modalTitle {
	font-family: "Inter", sans-serif;
	font-size: 16px;
	font-weight: 700;
	line-height: 24px;
	letter-spacing: 0em;
	text-align: left;
	color: rgba(20, 14, 52, 1);

	margin-bottom: 8px;
}
.modalText {
	font-family: "Inter", sans-serif;
	font-size: 12px;
	font-weight: 500;
	line-height: 21px;
	letter-spacing: 0em;
	text-align: left;
	color: rgba(20, 14, 52, 0.8);

	margin-bottom: 16px;
}
.modalBtns {
	display: flex;
	justify-content: space-between;
	width: 100%;
}
.modalBtns button {
	width: 110px;
	height: 37px;
	border-radius: 3px;
	font-family: "Inter", sans-serif;
	font-size: 14px;
	font-weight: 600;
	line-height: 24px;
	letter-spacing: 0em;
	text-align: center;
}
.modalNoBtn {
	border: 1px solid rgba(102, 112, 133, 0.5);
	background-color: #fff;
	color: rgba(20, 14, 52, 1);
}
.modalYesBtn {
	background-color: rgba(255, 102, 55, 1);
	color: #fff;
}
