.menuItem {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 45px;
	margin-bottom: 18px;
	background-color: #ff6637;

	color: #fff;
	font-size: 14px;
	font-weight: 600;
	font-family: "Inter", sans-serif;

	border-radius: 52px;
	padding: 0 1.5em;
	text-decoration: none;

	cursor: pointer;
}

.menuItem:last-child {
	margin-bottom: 0;
}
.svgDiv {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0 5px;
}
.menuItemChevron {
	margin-left: auto;
}
