.contentContainer {
	display: flex;
	flex-direction: column;
	margin: 20px;
	margin-bottom: 180px;
	height: calc(100% - 70px);
}

.noDataError {
	display: flex;
	align-items: center;
	font-family: "inter", sans-serif;
	font-weight: 500;
	font-size: 14px;
	color: #ffb346;
}

.data {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 16px 0;
}
.data span {
	display: block;
	text-align: center;
	word-wrap: break-word;
}

.employeeCardImg {
	display: flex;
	place-items: center;
	place-self: center;
	width: 300px;
	height: 436px;
	/* margin: 20px;
	padding: 20px 0; */
}
.employeeCardImg img {
	align-self: center;
	width: 100%;
}

/* .scanMsg {
	margin: 20px;
} */
.scanMsg span {
	display: flex;
	justify-content: center;
	align-items: center;
}
.success,
.alert {
	margin-bottom: 10px;
	font-weight: 600;
}
.alert {
	color: #ffb346;
}
.success {
	color: #20be79;
}
.successSvg {
	color: #20be79cc;
}
.alertSvg {
	color: #ffb346cc;
}

.postScanContainer {
	position: fixed;
	bottom: 0;
	width: 100%;
	display: flex;
	justify-content: center;
	padding: 7px 23px 26px 23px;
	background-color: #ffffff;
}
.postScan {
	display: flex;
	flex-direction: column;
	width: 100%;
}
.postScan .postScanBtn {
	display: flex;
	justify-content: center;
	align-items: center;

	font-family: "Inter", sans-serif;
	font-size: 14px;
	font-weight: 600;
	border-radius: 3px;
	text-decoration: none;

	height: 45px;
}

.scanNextBtn {
	background-color: #ff6637;
	color: #ffffff;
	margin-bottom: 16px;
}
.scanNextBtn:hover {
	background-color: #ff5b2a;
}
.doneBtn {
	background-color: #fff;
	color: #140e34;
	border: 1.5px solid rgba(20, 14, 52, 0.8);
}
.doneBtn:hover {
	background-color: #f8f8f8;
}
.postScanDisabled {
	background-color: #b2b2b2;
	color: #fff;
	border: none;
	pointer-events: none;
}
